html {
    box-sizing: border-box;
    scroll-behavior: smooth;
    min-width: map-get($breakpoints, "sm");
    position: relative;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

a {
    color: inherit;
}

button {
    cursor: pointer;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.nowrap {
    white-space: nowrap;
}
